.post-item {
  width: calc(50% - 10px);
  border-radius: 8px;
  margin-bottom: 20px;
  overflow: hidden;
  box-shadow: 5px 6px 14px rgba($color: #333232, $alpha: 1);
  .img-thumb {
    width: 100%;
    height: 200px;
    object-fit: cover;
  }
  .content-detail {
    padding: 16px;
    .title {
      margin: 0px;
      font-size: 20px;
      font-weight: bold;
      color: #333232;
    }
    .author {
      margin: 0px;
      margin-top: 14px;
      font-size: 10px;
      color: grey;
    }
    .body {
      margin: 0px;
      margin-top: 18px;
      font-size: 16px;
      color: grey;
    }
  }
}
